// import  getTrainInfo  from '../../common/request'
export default {
	// components:{
	// 	getTrainInfo
	// },
	data() {
		return {
					date: '2020-2-1',
					trainNumber: null,
					start_pos: null,
					end_pos:null,
					trainList:'',
					config:{},
					noResult:false
					
		}
	},
	onLoad() {
		this.getTrain();
	},
	methods: {

		async  post(url, data) {
			return new Promise(function(resolve, reject) {
				uni.request({
					url: getApp().globalData.hosturl + url,
					method: 'POST',
					header: {
						"content-type": 'application/x-www-form-urlencoded'
					},
					data,
					success: function(data) {
						resolve(data)
					},
					fail: function(err) {
						// 回调失败时
						console.log(err)
						if (typeof reject == 'function') {
							reject(data);
						} else {
							console.log(data);
						}
					}
				})
			})
		},
		  getTrainInfo() { // 参数：用户名
			return this.post('/all', {
			})
		},
		
		async getTrain(){
		 let data =	await this.getTrainInfo();
		 console.log("run...");
		 console.log(data);
		 console.log(data.data.code);
		 if (data.data.code == 200) {
		 	this.trainList = data.data.data;
			
			uni.setStorage({
			    key: 'trainList',
			    data: this.trainList,
			    success: function () {
			        console.log('success');
			    }
			});
		 
		 }
		},
		shareYourFriend(){
			// 分享给好友
			uni.showShareMenu();
		},
		queryInfos(){
		
			let date = this.date;
			let no = this.trainNumber;
			let strat_pos = this.start_pos;
			let end_pos = this.end_pos;
			return this.post('/condition', {
				date,
				no,
				strat_pos,
				end_pos
			})
		},
		async queryInfo(){
		 let data =	await this.queryInfos();
		 
		 if (data.data.code == 200) {
			
			if(data.data.data.length == 0){
				this.noResult = true;
			}
		 	this.trainList = data.data.data;
			uni.setStorage({
			    key: 'trainList',
			    data: this.trainList,
			    success: function () {
			        console.log('success');
			    }
			});
		 
		 }
		},
		DateChange(e) {
			this.date = e.detail.value
		},
		navToDetail(e){
			console.log(e);
			
			uni.navigateTo({
				url:'../detail/detail?id='+e
			})
		}
	}
}
